import React from 'react';
import styled from 'styled-components';
import { connect } from 'react-redux'
import { deleteComment } from '../../../../../../actions/comment'

const Container = styled.div`
    margin-bottom: 13px;
`

const ProfileAndCommentContainer = styled.div`
    display:flex;
`

const ProfileImage = styled.img`
    width: 30px;
    border-radius: 50%;
    height: 30px;
    margin-right: 12px;
`

const NameAndCommentContainer = styled.div``

const Name = styled.div`
    font-weight: 900;
`

const Comment = styled.div`
white-space: pre-line;
    padding: 5px;
    border-radius: 4px;
    background: white;
    margin-top: 6px;
`

const DeleteButton = styled.div`
margin-left: 42px;
    cursor: pointer;
    color: rgba(0,0,0,0.5);
    font-size: 12px;
    
    `

class CommentItem extends React.Component {
    render() {
        const { comment, user } = this.props;
        const { deleteButtonClicked } = this;
        return <Container>
            <ProfileAndCommentContainer>
                <ProfileImage src={comment.user.profilePhoto ? comment.user.profilePhoto : 'data:image/jpeg;base64,'} />
                <NameAndCommentContainer>
                    <Name>
                        {comment.user.name}
                    </Name>
                    <Comment>{comment.message}</Comment>
                </NameAndCommentContainer>
            </ProfileAndCommentContainer>
            {user.email === comment.user.email && <DeleteButton onClick={deleteButtonClicked}>Delete</DeleteButton>}

        </Container>
    }

    deleteButtonClicked = () => {
        const { deleteComment, comment } = this.props;
        deleteComment(comment.id)
    }

}

const mapStateToProps = state => {
    return {
        user: state.user
    }
}

export default connect(mapStateToProps, { deleteComment })(CommentItem)