import React from 'react';
import styled from 'styled-components';
import { connect } from 'react-redux'
import TextareaComponent from './Textarea';

const Container = styled.div`
    margin-bottom:30px;
`

const ProfileImage = styled.img`
    width:30px;
    height:30px;
    border-radius:50%;
    margin-right: 10px;
`

const TextareaContainer = styled.div`
    width: 496px;
    min-height: 38px;
    padding-left: 7px;
    padding-top: 5px;
    border-radius: 4px;
    background: white;
    cursor: pointer;
    box-shadow: 0 4px 8px -2px rgba(9,30,66,.25), 0 0 0 1px rgba(9,30,66,.08);
`

const ProfileImageAndTextAreaContainer = styled.div`
    display:flex;
    align-items:center;
    margin-top: 14px;
`


class InputComponent extends React.Component {
    state = {
        inputMode: false
    }
    render() {
        const { user } = this.props;
        const { inputMode } = this.state;
        const { turndownInputMode, turnonInputMode } = this;
        return <Container>
            <ProfileImageAndTextAreaContainer>
                <ProfileImage src={user.profilePhoto ? user.profilePhoto : 'data:image/jpeg;base64,'} />
                <TextareaContainer onClick={turnonInputMode}>
                    {inputMode ? <TextareaComponent turndownInputMode={turndownInputMode} /> : 'Add comment...'}
                </TextareaContainer>
            </ProfileImageAndTextAreaContainer>
        </Container>
    }

    turnonInputMode = () => {
        if (this.state.inputMode === false) {
            this.setState({
                inputMode: true
            })
        }

    }

    turndownInputMode = () => {
        this.setState({
            inputMode: false
        })
    }
}

const mapStateToProps = state => {
    return {
        user: state.user
    }
}

export default connect(mapStateToProps, {})(InputComponent)