import React from 'react';
import styled from 'styled-components';
import PopUpStatusBar from './PopupStatusbar/popupStatusbar';
import HomeIcon from './HomeIcon';
import { connect } from 'react-redux'
import { getUserInfo } from '../../../../actions/user'
import { changeProfileTrue } from '../../../../actions/changeProfile'
import { addNumberOneUnreadNotificationNumbers } from '../../../../actions/notification'
import BellIcon from './BellIcon';
import socketIOClient from 'socket.io-client'
import { getUnreadNotificationsNumber } from '../../../../actions/notification'
import { ENDPOINT } from '../../../../constants/endpoint';


const NavigationContainer = styled.div`
    width:100%;
    display:flex;
    justify-content:space-between;
background:${props => props.board ? 'rgba(0,0,0,.35)' : '#026aa7'};
    color:white;
    align-items:center;
    position:fixed;
    top:0;
    left:0;
    z-index: 1;
`

const LeftContainer = styled.div`
    display:flex;
`
const HomeIconContainer = styled.div`
margin-left:4px;
    background:hsla(0,0%,100%,.3);
    padding: 6px;
    border-radius: 4px;
    cursor: pointer;
    &:hover{
        background-color: hsla(0,0%,100%,.2);
    }
`




const MiddleContainer = styled.div`
    display:flex;
`

const MainTitleText = styled.div`
font-family: 'Libre Baskerville', serif;
font-family: 'Lobster', cursive;
user-select:none;
font-size: 27px;
    opacity: 0.5;
    &:hover {
        opacity:1;
    }
`


const RightContainer = styled.div`
    display:flex;
    align-items:center;
`

const ProfileImageContainer = styled.div`
    width:30px;
    height:30px;
    display:flex;
    justify-content:center;
    align-items:center;
    border-radius:50%;
    overflow:hidden;
    margin-right:8px;
    
`

const ProfileImage = styled.img`
    width:100%;
    height:100%;
    cursor: pointer;
    object-fit: cover;
`


class Navigation extends React.Component {
    state = {
        popupStatusBarVisible: false,
        socketConnection: 0,
        endpoint: ENDPOINT + ":8080"
    }

    componentDidMount() {
        const { getUserInfo, user, addNumberOneUnreadNotificationNumbers, getUnreadNotificationsNumber } = this.props;

        const socket = socketIOClient(this.state.endpoint)
        getUnreadNotificationsNumber();
        getUserInfo();
        console.log('user: ', user)
        if (user.email) {
            socket.emit('login', user)

            socket.on("unreadEmailNumbers", () => {
                addNumberOneUnreadNotificationNumbers()
            })
        }

    }

    componentWillReceiveProps(nextProps) {
        const { user } = nextProps;
        const { endpoint } = this.state;
        const { addNumberOneUnreadNotificationNumbers } = this.props;
        const socket = socketIOClient(endpoint)
        if (this.props.user !== nextProps.user) {
            if (user.email) {
                if (this.state.socketConnection === 0) {
                    console.log('login')
                    socket.emit('login', user)
                    socket.on("unreadEmailNumbers", () => {
                        console.log('unread email numbers!')
                        addNumberOneUnreadNotificationNumbers()
                    })
                    this.setState({
                        socketConnection: this.state.socketConnection + 1
                    })
                }

            }
        }
    }



    render() {
        const { popupStatusBarVisible } = this.state;
        const { makePopunInvisible, makePopupVisible } = this;
        const { profilePhoto } = this.props.user;
        const { board } = this.props;
        return (
            <NavigationContainer board={board}>
                <LeftContainer>
                    <HomeIconContainer>
                        <HomeIcon />
                    </HomeIconContainer>
                </LeftContainer>
                <MiddleContainer>
                    <MainTitleText>Treduler</MainTitleText>
                </MiddleContainer>
                <RightContainer>
                    <BellIcon />
                    <ProfileImageContainer onClick={makePopupVisible}>
                        <ProfileImage
                            src={profilePhoto ? profilePhoto : 'data:image/jpeg;base64,'}
                        />
                    </ProfileImageContainer>
                </RightContainer>
                <PopUpStatusBar makePopunInvisible={makePopunInvisible} popupStatusBar={popupStatusBarVisible} />
            </NavigationContainer>
        )
    }

    makePopupVisible = () => {
        console.log('profile photo clicked')
        if (this.state.popupStatusBarVisible === false) {

            this.setState({
                popupStatusBarVisible: true
            })
        }
        console.log(this.state.popupStatusBarVisible)
        const { changeProfileTrue } = this.props;
        // changeProfileTrue()
    }

    makePopunInvisible = () => {
        if (this.state.popupStatusBarVisible === true) {

            this.setState({
                popupStatusBarVisible: false
            })
        }
    }
}
const mapStateToProps = state => {
    return {
        user: state.user
    }
}

export default connect(mapStateToProps, { changeProfileTrue, getUserInfo, addNumberOneUnreadNotificationNumbers, getUnreadNotificationsNumber })(Navigation) 